:root {
	scrollbar-width: thin;
	scrollbar-color: var(--bgInverse2) transparent;
	scroll-behavior: smooth;

	--bg1: #e3e3e3;
	--bg2: #d7d7d7;
	--bgInverse: #595959;
	--bgInverse2: #444;

	--accent: #897666;

	--title: 'Distrela', serif;
	--titleL: 'Distrela Light', serif;
	--titleEL: 'Distrela Extra Light', serif;
	--titleSB: 'Distrela Semibold', serif;
	--titleB: 'Distrela Bold', serif;
	--titleEB: 'Distrela Extra Bold', serif;
	--title2: 'Chronicle', serif;
	--body: 'Noto Serif', serif;

	--lightOutline: var(--bg1) 1px 0px 0px, var(--bg1) 0.540302px 0.841471px 0px,
		var(--bg1) -0.416147px 0.909297px 0px, var(--bg1) -0.989993px 0.14112px 0px,
		var(--bg1) -0.653644px -0.756803px 0px,
		var(--bg1) 0.283662px -0.958924px 0px, var(--bg1) 0.96017px -0.279416px 0px;
	--darkOutline: var(--bgInverse) 1px 0px 0px,
		var(--bgInverse) 0.540302px 0.841471px 0px,
		var(--bgInverse) -0.416147px 0.909297px 0px,
		var(--bgInverse) -0.989993px 0.14112px 0px,
		var(--bgInverse) -0.653644px -0.756803px 0px,
		var(--bgInverse) 0.283662px -0.958924px 0px,
		var(--bgInverse) 0.96017px -0.279416px 0px;

	// --astorRender: url('../img/Viviiiii.png');
	--astorRender: url('../img/the-wall-02.png');
	--astorRender2: url('../img/Vivi-Transparent2.png');
	--astorBorder: url('../img/Vivi2.png');
	--astorCat: url('../img/Vivi-Transparent.png');
	--astorIcon: url('../img/Vivvvv.png');

	--roseImg: url('../img/Rose-Small.png');
	--roseFill: url('../img/Rose-Small-Fill.png');
}
body {
	margin: 0px;
	background-color: var(--bg1);
}
body.darkmode {
	--bg1: #222;
	--bg2: #2b2b2b;
	--bg3: #333;
	--bgInverse: #999;
	--bgInverse2: #aaa;
	--bgInverse3: #ccc;
	--lightOutline: var(--bg1) 1px 0px 0px, var(--bg1) 0.540302px 0.841471px 0px,
		var(--bg1) -0.416147px 0.909297px 0px, var(--bg1) -0.989993px 0.14112px 0px,
		var(--bg1) -0.653644px -0.756803px 0px,
		var(--bg1) 0.283662px -0.958924px 0px, var(--bg1) 0.96017px -0.279416px 0px;
	--darkOutline: var(--bgInverse) 1px 0px 0px,
		var(--bgInverse) 0.540302px 0.841471px 0px,
		var(--bgInverse) -0.416147px 0.909297px 0px,
		var(--bgInverse) -0.989993px 0.14112px 0px,
		var(--bgInverse) -0.653644px -0.756803px 0px,
		var(--bgInverse) 0.283662px -0.958924px 0px,
		var(--bgInverse) 0.96017px -0.279416px 0px;
}
.GID-hope {
	--accent: #b39a50;
}
.GID-fate {
	--accent: #986fa0;
}
.GID-unity {
	--accent: #a3757d;
}
.GID-liberty {
	--accent: #9da2cc;
}
.GID-fortune {
	--accent: #c77a5a;
}
.GID-entropy {
	--accent: #829199;
}
.GID-passion {
	--accent: #ba6a87;
}
.GID-knowledge {
	--accent: #b9adff;
}
.GID-vitality {
	--accent: #b28c70;
}
.GID-destruction {
	--accent: #9a8c98;
}
.GID-space {
	--accent: #a1b66f;
}
.GID-time {
	--accent: #b6565d;
}
.GID-order {
	--accent: #897666;
}
.GID-unbound {
	--accent: #b6565d;
}

.astor {
	padding: 50px;
	padding: clamp(35px, 5vw, 125px);
	color: var(--bgInverse);
	min-height: 100vh;
	box-sizing: border-box;
	position: relative;
	overflow: hidden;
}
.astor::before {
	content: '';
	display: block;
	position: absolute;
	top: 27px;
	left: 2px;
	right: 2px;
	background: linear-gradient(to bottom, var(--accent), transparent);
	background-position: bottom center;
	background-size: 500% 500%;
	height: 150px;
	pointer-events: none;
}
@media (max-width: 1149px) {
	.astor::before {
		background-size: 200% 200%;
	}
}
.astor::after {
	content: '';
	display: block;
	position: absolute;
	bottom: 2px;
	left: 2px;
	right: 2px;
	background: linear-gradient(to top, var(--accent), transparent);
	background-position: top center;
	background-size: 500% 500%;
	height: 600px;
	pointer-events: none;
}
.astor-border {
	position: fixed;
	top: 0px;
	left: 0px;
	right: 0px;
	height: 24px;
	z-index: 100000000;
	background-color: var(--bgInverse2);
	border-bottom: solid 1px var(--bgInverse2);
	box-shadow: 0px 2px 0px var(--bg1);
	transition: height 0.5s;
	overflow: hidden;
}
.astor-border div {
	background-color: var(--accent);
	background-image: var(--astorBorder);
	background-size: cover;
	background-position: bottom;
	mix-blend-mode: lighten;
	width: 100%;
	height: 100%;
	transition: height 0.5s;
}
.darkmode .astor-border {
	border-color: var(--bgInverse3);
	background-color: var(--accent);
}
.darkmode .astor-border div {
	background-color: var(--bgInverse3);
}

.astor-menu-back {
	background-color: var(--bg1);
	position: fixed;
	z-index: 10;
	bottom: 0px;
	left: 0px;
	right: 0px;
	top: 27px;
	opacity: 0;
	transition: opacity 0.5s;
	pointer-events: none;
}
.menu .astor-menu-back {
	opacity: 1;
}

.astor-menu-bulk {
	background-color: var(--bg1);
	padding: clamp(35px, 5vw, 125px);
	display: grid;
	grid-template-rows: 210px auto;
	gap: 50px;
	position: fixed;
	z-index: 11;
	top: 27px;
	left: 0px;
	right: 0px;
	bottom: 0px;
	pointer-events: none;
	opacity: 0;
	transition:
		top 0.5s,
		opacity 0.5s;
	font: 13px / 20px var(--body);
	text-align: justify;
	letter-spacing: 0.25px;
}
.menu .astor-menu-bulk {
	top: -23px;
	opacity: 1;
	pointer-events: auto;
}
.astor-menu-name {
	margin: -25px 0px 0px 0px;
	padding-bottom: 15px;
	position: relative;
	display: flex;
	gap: 50px;
	align-items: flex-end;
	justify-content: space-between;
}
.astor-menu-name span {
	font: 121px / 85px var(--titleEB);
	font: 100px / 85px var(--title2);
	text-transform: uppercase;
	display: block;
}
.astor-menu-name div {
	color: var(--accent);
}
.astor-menu-name p {
	margin: 0px 0px -18px 0px;
	padding: 0px;
	text-align: right;
}
.astor-menu-name p b,
.astor-menu-name p a {
	text-transform: uppercase;
	font-weight: 800 !important;
}
.astor-menu-name p a {
	text-decoration: none;
	color: var(--accent);
	transition: color 0.5s;
}
.astor-menu-name p a:hover {
	color: var(--bgInverse);
}
.darkmode .astor-menu-name p b,
.darkmode .astor-menu-name p a:hover {
	color: var(--bgInverse3);
}
.astor-menu-bulk ul {
	list-style-type: none;
	position: absolute;
	z-index: 1;
	top: clamp(35px, 5vw, 125px);
	left: clamp(35px, 5vw, 125px);
	margin: 25px 0px 0px -60px;
}
.astor-menu-bulk li:nth-child(1) {
	display: block;
	height: 60px;
	width: 88px;
	position: absolute;
	mask-image: var(--roseFill);
	mask-repeat: no-repeat;
}
.astor-menu-bulk li:nth-child(1)::before {
	content: '';
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: var(--accent);
}
.astor-menu-bulk li:nth-child(2) {
	display: block;
	height: 60px;
	width: 88px;
	position: absolute;
	mask-image: var(--roseImg);
	mask-repeat: no-repeat;
}
.astor-menu-bulk li:nth-child(2)::before {
	content: '';
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: var(--bgInverse2);
}
.darkmode .astor-menu-bulk li:nth-child(1)::before {
	background-color: var(--bg1);
}
.darkmode .astor-menu-bulk li:nth-child(2)::before {
	background-color: var(--accent);
}
.astor-menu-border {
	border: solid 1px var(--bgInverse);
	outline: solid 4px var(--bg1);
	outline-offset: -5px;
	background-color: var(--bgInverse);
	grid-column: 1 / 4;
}
.astor-menu-border div {
	height: 100%;
	width: 100%;
	background-color: var(--accent);
	mix-blend-mode: lighten;
	background-image: var(--astorBorder);
	background-position: center;
	background-size: cover;
}

.astor-menu-grid {
	display: grid;
	grid-template-rows: auto 1fr;
	grid-template-columns: 1fr 1fr 1fr;
	gap: 50px;
}
.astor-menu-links h1 {
	border-bottom: solid 1px var(--accent);
	display: flex;
	gap: 15px;
	height: 5px;
	margin: 10px 0px 35px 0px;
}
.astor-menu-links h1 div {
	background-color: var(--bg1);
	padding: 0px 10px 0px 13px;
	font: 800 10px / 10px var(--body);
	text-transform: uppercase;
	letter-spacing: 2px;
	margin-bottom: -5px;
	white-space: nowrap;
}
.astor-menu-links h1 div:nth-child(1) {
	font: 26px / 10px var(--titleEB);
	text-transform: lowercase;
	color: var(--accent);
	padding: 0px 10px 5px 0px;
	margin-top: -6px;
}
.astor-menu-links h1 + div {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 10px 35px;
}
.astor-menu-links a {
	text-transform: uppercase;
	text-decoration: none;
	font: 800 11px / 10px var(--body);
	letter-spacing: 0.5px;
	color: var(--bgInverse);
	white-space: nowrap;
	transition: color 0.5s;
}
.astor-menu-links a:hover {
	color: var(--accent);
}
.astor-menu-sub {
	display: flex !important;
	gap: 10px !important;
	flex-wrap: wrap;
	max-height: 138px;
	overflow: auto;
	padding-right: 10px;
	scrollbar-color: var(--accent) transparent;
	scrollbar-width: thin;
}
.astor-menu-sub a {
	display: block;
	border: solid 1px var(--bgInverse);
	height: 58px;
	width: 58px;
	outline: solid 2px var(--bg1);
	outline-offset: -3px;
	padding: 2px;
	background-color: var(--bgInverse);
}
.astor-menu-sub a div {
	height: 100%;
	width: 100%;
	background-color: var(--accent);
	background-image: var(--astorIcon);
	background-size: cover;
	background-position: center;
	mix-blend-mode: lighten;
	background-blend-mode: multiply;
	transition: background-color 0.5s;
}
.astor-menu-sub a:hover div {
	background-color: #fff;
}
.darkmode .astor-menu-sub a,
.darkmode .astor-menu-border {
	mix-blend-mode: lighten;
}
.darkmode .astor-menu-sub a div,
.darkmode .astor-menu-border div {
	mix-blend-mode: normal;
}
@media (max-width: 1149px) {
	.menu .astor-menu-bulk {
		display: block;
	}
	.astor-menu-bulk {
		padding: 50px;
		height: calc(100vh - 80px);
		overflow: auto;
		scrollbar-color: var(--accent) transparent;
		scrollbar-width: thin;
	}
	.astor-menu-name {
		margin-top: 50px;
		flex-direction: column;
		align-items: flex-start;
		gap: 25px;
		margin-bottom: 35px;
	}
	.astor-menu-name span {
		font: 42px / 30px var(--titleEB);
		font: 36px / 30px var(--title2);
		white-space: nowrap;
	}
	.astor-menu-name p {
		text-align: left;
	}
	.astor-menu-bulk ul {
		display: none;
	}
	.astor-menu-grid {
		display: flex;
		flex-direction: column;
		gap: 25px;
	}
	.astor-menu-border {
		display: none;
	}
}

.astor-menu {
	position: fixed;
	z-index: 100000001;
	top: clamp(35px, 5vw, 125px);
	margin-top: 25px;
	right: clamp(35px, 5vw, 125px);
	border: solid 1px var(--bgInverse);
	background-color: var(--bg1);
	font: 800 11px / 10px var(--body);
	text-transform: uppercase;
	letter-spacing: 1px;
	outline: solid 2px var(--bg1);
	display: flex;
	transition: margin-top 0.5s;
}
.astor-menu-left {
	position: relative;
	padding: 14px;
	box-shadow: 1px 0px 0px var(--bgInverse);
	margin-right: 1px;
	background: linear-gradient(-135deg, var(--bgInverse2) 25%, var(--accent));
	background-size: 400% 100%;
	background-position: 75% 0%;
	outline: solid 2px var(--bg1);
	color: var(--bg1);
	outline-offset: -2px;
	cursor: pointer;
	transition:
		background-color 0.5s,
		color 0.5s;
}
.astor-menu-left:hover {
	color: var(--accent);
}
.astor-menu-left b {
	position: relative;
	z-index: 1;
}
.astor-menu-left b::before {
	content: 'full';
}
.menu .astor-menu-left b::before {
	content: 'close';
}
.astor-menu-right {
	display: flex;
	gap: 7px;
	align-items: center;
	padding: 1px 12px 0px 12px;
	font-size: 13px;
	background-color: var(--bgInverse2);
	outline: solid 2px var(--bg1);
	outline-offset: -2px;
}
.astor-menu-right a {
	cursor: pointer;
	transition: color 0.5s;
	text-decoration: none;
	color: var(--bg1);
}
.astor-menu-right a:hover {
	color: var(--accent);
}
.darkmode .astor-menu-left {
	background: var(--accent);
}
.darkmode .astor-menu-right {
	background-color: var(--bgInverse3);
}
.darkmode .astor-menu-left:hover {
	background-color: var(--bgInverse3);
	color: var(--bg1);
}
@media (max-width: 1149px) {
	.astor-border {
		height: 38px;
	}
	.astor-menu {
		top: -1px;
		right: -1px;
		margin-top: 0px;
		background-color: transparent;
		outline: none;
		color: var(--bg1);
		border-color: transparent;
	}
	.astor-menu-left {
		outline: none;
		background: transparent !important;
		box-shadow: none;
		padding-right: 0px;
	}
	.astor-menu-left b {
		text-shadow: var(--darkOutline);
	}
	.astor-menu-right {
		gap: 12px;
		outline: none;
		background-color: transparent !important;
	}
	.astor-menu-right a {
		color: var(--bg1);
		font-size: 14px;
	}
	.astor-menu-right a:first-of-type,
	.astor-menu-right a:last-of-type {
		display: none;
	}
	.darkmode .astor-menu-left:hover,
	.darkmode .astor-menu-right a:hover {
		color: var(--bgInverse3);
	}
}

.astor-header {
	position: relative;
	height: 350px;
}
.astor-header::before {
	content: '';
	display: block;
	position: absolute;
	bottom: 0px;
	left: 550px;
	right: 0px;
	border-bottom: solid 1px var(--bgInverse);
}
.astor-render {
	mask-image: var(--astorRender);
	mask-size: 50% 100%;
	mask-repeat: no-repeat;
	mask-position: top left;
	-webkit-mask-image: var(--astorRender);
	-webkit-mask-size: auto 100%;
	-webkit-mask-repeat: no-repeat;
	-webkit-mask-position: top left;
	height: 1000px;
	width: 700px;
	position: absolute;
	top: -300px;
	left: -75px;
	pointer-events: none;
}
.astor-render::before {
	content: '';
	display: block;
	position: absolute;
	top: 0px;
	left: 0px;
	right: 0px;
	bottom: 0px;
	height: 100%;
	width: 100%;
	background: linear-gradient(
		to bottom,
		var(--bgInverse2) 400px,
		var(--accent)
	);
	background-size: 150% 150%;
	background-position: top center;
}
.darkmode .astor-render::before {
	background: linear-gradient(
		to bottom,
		var(--bgInverse2) 500px,
		var(--accent)
	);
	background-size: 100% 100%;
}

.astor-header input {
	display: none;
}
.astor-header-label {
	position: absolute;
	outline: solid 2px var(--bg1);
	outline-offset: -3px;
	bottom: 60px;
	right: 0px;
	border: solid 1px var(--bgInverse);
	padding: 13px 0px 15px 0px;
	font: 16px / 10px var(--titleEB);
	letter-spacing: 1.5px;
	width: 100px;
	text-align: center;
	cursor: pointer;
	background-color: var(--bg1);
	text-shadow: var(--lightOutline);
	background: linear-gradient(-135deg, transparent 25%, var(--accent));
	background-size: 400% 100%;
	background-position: 99% 0%;
	transition:
		background-position 0.5s,
		color 0.5s;
}
.astor-header-label:hover,
.astor-header input:checked + label {
	background-position: 75% 0%;
	color: var(--accent);
}
@media (max-width: 1149px) {
	.astor-header-label {
		display: none;
	}
}

.astor-name {
	font: 175px / 175px var(--titleEB);
	position: absolute;
	left: 465px;
	bottom: 100px;
	opacity: 0;
	text-shadow: var(--darkOutline);
	color: var(--bg1);
	transition:
		bottom 0.5s,
		opacity 0.5s;
	pointer-events: none;
}
.astor-name span:last-of-type {
	display: none;
	font: 100px / 75px var(--titleEB);
	text-align: center;
}
#astor-header-tab-1:checked ~ .astor-name {
	opacity: 1;
	bottom: 60px !important;
	pointer-events: auto;
}
.astor-name::before {
	padding: 50px 0px;
	content: 'nastene.ru';
	display: block;
	position: absolute;
	bottom: -50px;
	background: linear-gradient(-135deg, transparent 50%, var(--accent));
	background-clip: text;
	color: transparent;
	text-shadow: none;
	background-size: 300% 100%;
	background-position: 100% 0%;
	transition: background-position 0.5s;
	-webkit-text-stroke: 3px #fff;
	mix-blend-mode: multiply;
}
.astor-name div:nth-child(1) {
	height: 60px;
	width: 88px;
	position: absolute;
	top: 55px;
	left: 450px;
	mask-image: var(--roseFill);
	mask-repeat: no-repeat;
}
.astor-name div:nth-child(1)::before {
	content: '';
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: var(--accent);
}
.astor-name div:nth-child(2) {
	height: 60px;
	width: 88px;
	position: absolute;
	top: 55px;
	left: 450px;
	mask-image: var(--roseImg);
	mask-repeat: no-repeat;
}
.astor-name div:nth-child(2)::before {
	content: '';
	position: absolute;
	height: 100%;
	width: 100%;
	background-color: var(--bgInverse2);
}
.darkmode .astor-name div:nth-child(1)::before {
	background-color: var(--bg1);
}
.darkmode .astor-name div:nth-child(2)::before {
	background-color: var(--accent);
}
.darkmode .astor-name::before {
	mix-blend-mode: lighten;
